@import '../../utility.scss';
.sectionHolder {
  &__title {
    text-transform: capitalize;
    letter-spacing: 0.1rem;
    font-size: 3.2rem;
  }
  &__data {
    height: 95%;
    overflow-y: scroll;

    .userTours {
      padding: 2rem 2rem 2rem 1rem;
      @include respond(phone) {
        padding: 0;
      }
    }
  }
}
